<template>
    <div>
        <form @submit.prevent="addplayerData">
            <label>门派 <input type="text" :placeholder="myplayerData.userName" v-model="myplayerData.userName"></label>
            <label>技能1 <input type="number" :placeholder="myplayerData.skills.skill1" v-model.number="myplayerData.skills.skill1"></label>
            <label>技能2 <input type="number" :placeholder="myplayerData.skills.skill2" v-model.number="myplayerData.skills.skill2"></label>
            <label>技能3 <input type="number" :placeholder="myplayerData.skills.skill3" v-model.number="myplayerData.skills.skill3"></label>
            <label>技能4 <input type="number" :placeholder="myplayerData.skills.skill4" v-model.number="myplayerData.skills.skill4"></label>
            <label>技能5 <input type="number" :placeholder="myplayerData.skills.skill5" v-model.number="myplayerData.skills.skill5"></label>
            <label>技能6 <input type="number" :placeholder="myplayerData.skills.skill6" v-model.number="myplayerData.skills.skill6"></label>
            <label>技能7 <input type="number" :placeholder="myplayerData.skills.skill7" v-model.number="myplayerData.skills.skill7"></label>

    
            <label>攻修 <input type="number" :placeholder="myplayerData.exSkills.skill1" v-model.number="myplayerData.exSkills.skill1"></label>
            <label>法修 <input type="number" :placeholder="myplayerData.exSkills.skill2" v-model.number="myplayerData.exSkills.skill2"></label>
            <label>物抗 <input type="number" :placeholder="myplayerData.exSkills.skill3" v-model.number="myplayerData.exSkills.skill3"></label>
            <label>魔抗 <input type="number" :placeholder="myplayerData.exSkills.skill4" v-model.number="myplayerData.exSkills.skill4"></label>
    
            <label>宝宝攻修 <input type="number" :placeholder="myplayerData.bbSkills.skill1" v-model.number="myplayerData.bbSkills.skill1"></label>
            <label>宝宝法修 <input type="number" :placeholder="myplayerData.bbSkills.skill2" v-model.number="myplayerData.bbSkills.skill2"></label>
            <label>宝宝物抗 <input type="number" :placeholder="myplayerData.bbSkills.skill3" v-model.number="myplayerData.bbSkills.skill3"></label>
            <label>宝宝魔抗 <input type="number" :placeholder="myplayerData.bbSkills.skill4" v-model.number="myplayerData.bbSkills.skill4"></label>
    
    
            <button type="submit" v-show="!myplayerData.id">添加</button>
            <button v-show="myplayerData.id" @click.prevent="saveHandle" type="save">保存</button>
    
        </form>
    </div>
</template>

<script>

import {nanoid} from 'nanoid'
export default {
    name: 'EditplayerData',
    props: {
        addDataHandle: {
            require: false
        },
        updataHandle: {
            require: false
        },
        playerData: {
            Object,
            require: true
        },
    },
    data() {
        return {
            myplayerData: this.playerData,
        }
    },
    methods: {
        addplayerData(){
            var r = confirm("确定要提交吗？");
            if (r) {
                this.myplayerData.id = nanoid();
                this.addDataHandle(this.myplayerData)
            }
        },
        saveHandle(){
            var r = confirm("确定要修改吗？");
            if (r) {
                this.updataHandle(this.myplayerData)
            }
        }
    },
}
</script>

<style scoped>
label {
    display: block;
}
</style>