<template>
    <div>
        <!-- <AddPlayerInfo></AddPlayerInfo> -->
        <EditPlayerInfo class="addPlayer" :playerData="{
            userName: '',
            skills:
            {
                skill1: 0,
                skill2: 0,
                skill3: 0,
                skill4: 0,
                skill5: 0,
                skill6: 0,
                skill7: 0
            },
            exSkills: {
                skill1: 0,
                skill2: 0,
                skill3: 0,
                skill4: 0,
            },
            bbSkills: {
                skill1: 0,
                skill2: 0,
                skill3: 0,
                skill4: 0,
            }
        }"
        :addDataHandle="addPlayerInfoCallBack"
        />

        <EditPlayerInfo v-for="(item,index) in playerList" :playerData="item" :key="index" :updataHandle="updatedPlayerInfo"/>
    </div>
</template>

<script>
import EditPlayerInfo from './EditPlayerInfo.vue';
// import PlayerInfoItem from './PlayerInfoItem.vue';
export default {
    name: 'PlayerInfo',
    components:{EditPlayerInfo},
    data() {
        return {
            playerList: JSON.parse(localStorage.getItem("playerList")) || []
        }  
    },
    watch: {
        playerList: {
            deep: true,
            handler(newValue){
                var str = JSON.stringify(newValue)
                localStorage.setItem("playerList", str)
            }
        }
    },

    methods: {
        addPlayerInfoCallBack(item){
            console.log(item);
            this.playerList.push(item)
        },

        updatedPlayerInfo(newItem) {
            this.playerList.forEach(item => {
                if (item.id === newItem.id) {
                    item = newItem
                }
            });
        },
    },
}
</script>

<style scoped>
    .addPlayer {
        border: 1px black solid;
        margin: 20px auto;
    }
</style>
